import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import VueCookie from 'vue-cookie'
import '@/common/js/directive.js'
import { DatetimePicker , Radio ,Popup, InfiniteScroll} from 'mint-ui'
import { Button,Circle,PullRefresh,Search ,Stepper ,Toast , Dialog } from 'vant'
import 'ml-ui/styles/index.css'
import MlUi  from 'ml-ui'
Vue.config.productionTip = false
Vue.component(DatetimePicker.name, DatetimePicker);
Vue.component(Popup.name, Popup);
Vue.use(Circle).use(Button).use(PullRefresh).use(Search).use(Stepper).use(Toast).use(Dialog);
Vue.use( VueCookie );
Vue.use(MlUi)
Vue.use(InfiniteScroll);
axios.interceptors.request.use(function (config){
  // 处理请求之前的配置
  console.log(config)
/*
  config.params.uid = '222222222222222222222222'
  config.params.token = 'sjadaksdhakjsd23242'
*/
  return config;
}, function (error){
  // 请求失败的处理
  return Promise.reject(error);
});
// 响应拦截（配置请求回来的信息）
axios.interceptors.response.use(function (response){
  // 处理响应数据
  if(response.data.status==401){
      Toast(response.data.message)
    VueCookie.delete('accessToken');
    VueCookie.delete('cellphone');
    VueCookie.delete('id');
    router.replace('/user')
  }
  return response;
}, function (error){
  // 处理响应失败
  router.app.$children[0].Status = false
  return Promise.reject(error);
});
/* eslint-disable no-new */

//全局过滤器
Vue.filter("moneyType",function (value) {
  return Number(value).toFixed(2);
});




Vue.prototype.$bus=new Vue();
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})




