import Vue from 'vue'
import Router from 'vue-router'
//主页
const Home = (resolve) => { import('@/components/Home').then((module) => { resolve(module) }) }
//底部导航
const Footer = (resolve) => { import('@/components/Footer').then((module) => { resolve(module) }) }
//商城首页
const Mall = (resolve) => { import('@/components/Mall').then((module) => { resolve(module) }) }
//预约页
const Yuyue = (resolve) => { import('@/components/Yuyue').then((module) => { resolve(module) }) }
//保险详情页
const Xiangqin = (resolve) => { import('@/components/xiangqin').then((module) => { resolve(module) }) }
//账户管理
const CountManage = (resolve) => { import('@/components/countManege').then((module) => { resolve(module) }) }
//积分充值(选积分)
const jfCharge = (resolve) => { import('@/components/jf_charge').then((module) => { resolve(module) }) }
//修改手机号
const xgMobile = (resolve) => { import('@/components/xg_mobile').then((module) => { resolve(module) }) }
//未登录时更改密码
const unSafe = (resolve) => { import('@/components/UnSafe').then((module) => { resolve(module) }) }
//商品详情
const spXiangqin = (resolve) => { import('@/components/sp_xiangqin').then((module) => { resolve(module) }) }
//收益概况
const IncomeProfile = (resolve) => { import('@/components/sxProfile').then((module) => { resolve(module) }) }
//支付结果
const payResult = (resolve) => { import('@/components/payresult').then((module) => { resolve(module) }) }
//确认订单
const sDingdan = (resolve) => { import('@/components/sure_dingdan').then((module) => { resolve(module) }) }
//确认兑换
const sPay = (resolve) => { import('@/components/paysure').then((module) => { resolve(module) }) }
//保险预约
const bxYuyue = (resolve) => { import('@/components/bx_yuyue').then((module) => { resolve(module) }) }
//商城搜索
const scSearch = (resolve) => { import('@/components/sc_search').then((module) => { resolve(module) }) }
//新增或查看银行卡
const noCard = (resolve) => { import('@/components/xz_yinghangka').then((module) => { resolve(module) }) }
//商城栏目
const sclanMu = (resolve) => { import('@/components/sc_lanmu').then((module) => { resolve(module) }) }
//积分充值(输入积分)
const jfChongzhi = (resolve) => { import('@/components/jf_chongzhi').then((module) => { resolve(module) }) }
//我的收益
const wdShouyi = (resolve) => { import('@/components/wd_shouyi').then((module) => { resolve(module) }) }
//我的分销收益
const MyDistruIncome = (resolve) => { import('@/components/MyDistrubIncome').then((module) => { resolve(module) }) }
//预约详情
const yyXiangqin = (resolve) => { import('@/components/yy_xiangqin').then((module) => { resolve(module) }) }
//积分明细
const jfMingxi = (resolve) => { import('@/components/jf_mingxi').then((module) => { resolve(module) }) }
//积分详情
const jfDetail = (resolve) => { import('@/components/jf_detail').then((module) => { resolve(module) }) }

//收益详情
const IncomeDetail = (resolve) => { import('@/components/IncomeDetail').then((module) => { resolve(module) }) }
//修改兑换密码
const xgDuipassword = (resolve) => { import('@/components/xg_duipassword').then((module) => { resolve(module) }) }
//修改登录密码
const xgDengpassword = (resolve) => { import('@/components/xg_dengpassword').then((module) => { resolve(module) }) }
//商城分类选择
const scKind = (resolve) => { import('@/components/sc_kind').then((module) => { resolve(module) }) }
//手机安全页(发送手机验证码验证)
const Safe = (resolve) => { import('@/components/Safe').then((module) => { resolve(module) }) }
//注册页
const Register = (resolve) => { import('@/components/Register').then((module) => { resolve(module) }) }
//用户页(父级路由)
const User = (resolve) => { import('@/components/user/index').then((module) => { resolve(module) }) }
//空白页
const Nothing = (resolve) => { import('@/components/nothing').then((module) => { resolve(module) }) }
//登录页
const UserLogout = (resolve) => { import('@/components/user/logout/logout').then((module) => { resolve(module) }) }
//个人中心页
const UserLogin = (resolve) => { import('@/components/user/login/login').then((module) => { resolve(module) }) }

//保险列表搜索
const insuranceSearch = (resolve) => { import('@/components/insurance_search').then((module) => { resolve(module) }) }
//搜索列表（公共）
const searchList = (resolve) => { import('@/components/searchList').then((module) => { resolve(module) }) }


/**商城订单管理模块**/
  //商城订单列表
const mallorderList = (resolve) => { import('@/components/mallOrder/mallorderList').then((module) => { resolve(module) }) }
  //商城订单详情
const orderDetail = (resolve) => { import('@/components/mallOrder/orderDetail').then((module) => { resolve(module) }) }
  //订单物流信息
const logisticsInfo = (resolve) => { import('@/components/mallOrder/logisticsInfo').then((module) => { resolve(module) }) }

/**个人信息模块**/
  //个人信息列表
const userInfo = (resolve) => { import('@/components/userInfo/userInfo').then((module) => { resolve(module) }) }
  //地址管理
const addressList = (resolve) => { import('@/components/userInfo/addressList').then((module) => { resolve(module) }) }
  //添加、编辑地址
const addressOption = (resolve) => { import('@/components/userInfo/addressOption').then((module) => { resolve(module) }) }
  //银行卡管理
const creditCardList = (resolve) => { import('@/components/userInfo/creditCardList').then((module) => { resolve(module) }) }
  //添加、编辑银行卡
const crediteCardOption = (resolve) => { import('@/components/userInfo/crediteCardOption').then((module) => { resolve(module) }) }

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      meta: {
        "title":"安富智享"
      },
      components: {
        content: Home,
        footer: Footer
      }
    },
    {
      path: '/home',
      name: 'home',
      meta: {
        "title":"安富智享"
      },
      components: {
        content: Home,
        footer: Footer
      }
    },
    {
      path: '/xiangqin',
      name: 'xiangqin',
      meta: {
        "title":"保险详情"
      },
      components: {
        content: Xiangqin,
      }
    },
    {
      path: '/scSearch',
      name: 'scSearch',
      meta: {
        "title":"安富智享"
      },
      components: {
        content: scSearch,
      }
    },
    {
      path: '/noCard',
      name: 'noCard',
      meta: {
        "title":"安富智享"
      },
      components: {
        content: noCard,
      }
    },
    {
      path: '/yyXiangqin/:mark/:id',
      name: 'yyXiangqin',
      meta: {
        "title":"预约详情"
      },
      components: {
        content: yyXiangqin,
      }
    },
    {
      path: '/payResult',
      name: 'payResult',
      meta: {
        "title":"确认兑换"
      },
      components: {
        content: payResult,
      }
    },
    {
      path: '/bxYuyue',
      name: 'bxYuyue',
      meta: {
        "title":"保险预约"
      },
      components: {
        content: bxYuyue,
      }
    },
    {
      path: '/CountManage',
      name: 'CountManage',
      meta: {
        "title":"账号管理"
      },
      components: {
        content: CountManage,
      }
    },
    {
      path: '/spXiangqin/:id',
      name: 'spXiangqin',
      meta: {
        "title":"商品详情"
      },
      components: {
        content: spXiangqin,
      }
    },
    {
      path: '/sDingdan/:id&:title&:jifen&:num',
      name: 'sDingdan',
      meta: {
        "title":"确认订单"
      },
      components: {
        content: sDingdan,
      }
    },
    {
      path: '/wdShouyi',
      name: 'wdShouyi',
      meta: {
        "title":"我的收益"
      },
      components: {
        content: wdShouyi,
      }
    },
    {
      path: '/sPay',
      name: 'sPay',
      meta: {
        "title":"确认兑换"
      },
      components: {
        content: sPay,
      }
    },
    {
      path: '/mall',
      meta: {
        "title":"商城"
      },
      components: {
        content:Mall,
        footer:Footer
      }
    },
    {
      path: '/safe:typeId',
      name:'safe',
      meta: {
        "title":"安全验证"
      },
      components: {
        content:Safe,
      }
    },
    {
      path: '/scKind',
      meta: {
        "title":"商城分类"
      },
      components: {
        content:scKind,
      }
    },
    {
      path: '/scLanmu',
      name:'scLanmu',
      meta: {
        "title":"商城栏目"
      },
      components: {
        content:sclanMu,
      }
    },
    {
      path: '/xgMobile',
      meta: {
        "title":"修改手机号"
      },
      components: {
        content:xgMobile,
      }
    },
    {
      path: '/MyDistruIncome',
      meta: {
        "title":"我的收益"
      },
      components: {
        content:MyDistruIncome,
      }
    },
    {
      path: '/jfCharge',
      meta: {
        "title":"积分充值"
      },
      components: {
        content:jfCharge,
      }
    },
    {
      path: '/xgDuipassword',
      meta: {
        "title":"设置兑换密码"
      },
      components: {
        content:xgDuipassword,
      }
    },
    {
      path: '/unSafe',
      meta: {
        "title":"安全验证"
      },
      components: {
        content:unSafe,
      }
    },
    {
      path: '/xgDengpassword',
      meta: {
        "title":"设置登录密码"
      },
      components: {
        content:xgDengpassword,
      }
    },
    {
      path: '/jfChongzhi',
      meta: {
        "title":"积分充值"
      },
      components: {
        content:jfChongzhi,
      }
    },
    {
      path: '/jfMingxi',
      meta: {
        "title":"积分明细"
      },
      components: {
        content:jfMingxi,
      }
    },
    {
      path: '/jfDetail',
      meta: {
        "title":"积分详情"
      },
      components: {
        content:jfDetail,
      }
    },
    {
      path: '/register',
      meta: {
        "title":"注册"
      },
      components: {
        content:Register
      }
    },
    {
      path: '/yuyue',
      name:'yuyue',
      meta: {
        "title":"预约"
      },
      components: {
        content:Yuyue
      }
    },
    {
      path: '/user',
      components: {
        content:User
      },
      children:[
        {
          path:'/',
          meta: {
            "title":"登录"
          },
          component:UserLogout
        },
        {
          path:'login',
          meta: {
            "title":"个人中心"
          },
          name:'user',
          component:UserLogin
        }
      ],
    },
    {

      path: '/nothing',
      meta: {
        "title":"找不到页面"
      },
      components: {
        content:Nothing,
      }
    },
      {
      path: '/mallOrder',
        name:'mallOrder',
      meta: {
        "title":"商城订单"
      },
      components: {
        content:mallorderList
      }
    },
    {
      path: '/orderDetail',
      meta: {
        "title":"订单详情"
      },
      components: {
        content:orderDetail
      }
    },
    {
      path: '/logisticsInfo',
      meta: {
        "title":"物流信息"
      },
      components: {
        content:logisticsInfo
      }
    },
    {
      path: '/userInfo',
      meta: {
        "title":"会员信息"
      },
      components: {
        content:userInfo
      }
    },
    {
      path: '/addressList',
      meta: {
        "title":"地址管理"
      },
      components: {
        content:addressList
      }
    },
    {
      path: '/addAddress',
      meta: {
        "title":"添加地址"
      },
      components: {
        content:addressOption
      }
    },
    {
      path: '/editAddress',
      meta: {
        "title":"编辑地址"
      },
      components: {
        content:addressOption
      }
    },
    {
      path: '/creditCardList',
      meta: {
        "title":"银行卡"
      },
      components: {
        content:noCard
      }
    },
    {
      path: '/addCreditCard',
      meta: {
        "title":"添加银行卡"
      },
      components: {
        content:crediteCardOption
      }
    },
    {
      path: '/editCreditCard',
      meta: {
        "title":"编辑银行卡"
      },
      components: {
        content:crediteCardOption
      }
    },
    {
      path: '/IncomeDetail',
      meta: {
        "title":"收益详情"
      },
      components: {
        content:IncomeDetail
      }
    },
    {
      path: '/IncomeProfile',
      meta: {
        "title": "收益概况"
      },
      components: {
        content: IncomeProfile,
      }
    },
    {
      path: '/insuranceSearch',
      meta: {
        "title": "保险搜索"
      },
      components: {
        content: insuranceSearch
      }
    },
    {
      path: '/searchList',
      name:'searchList',
      meta: {
        "title": "搜索"
      },
      components: {
        content: searchList
      }
    }
  ],
  mode:'history'
})



