import Vue from 'vue'
Vue.directive('chiose', {
  // 当被绑定的元素插入到 DOM 中时……
  inserted: function (el) {
    // 聚焦元素
    el.onclick = function (e) {
      if(e.target.classList.contains('chiose')==false) {
        e.target.classList.add('chiose');
      }else {
        e.target.classList.remove('chiose');
      }
    }
  }
})

Vue.directive('focus', {
  // 当被绑定的元素插入到 DOM 中时……
  inserted: function (el) {
    // 聚焦元素
    el.onfocus = function (e) {
      e.target.parentNode.classList.add('focus')
    },
      el.onblur = function (e) {
        e.target.parentNode.classList.remove('focus')
      }
  }
})

Vue.directive('focuss', {
  // 当被绑定的元素插入到 DOM 中时……
  inserted: function (el) {
    // 聚焦元素
    el.focus()
  }
})



