<template>
  <div id="app">
    <div class="content" v-if="Status">
      <router-view name="content"></router-view>
    </div>
      <router-view v-if="Status" name="footer"></router-view>
    <Nothing v-if="!Status"></Nothing>
  </div>
</template>

<script>
  import Nothing from '@/components/nowifi'
export default {
  name: 'App',
  data(){
    return{
      Status:true
    }
  },
  watch: {
    $route (to, from) {
      document.title = to.meta.title;
    }
  },
  components:{
    Nothing
  },
  mounted(){
    this.$cookie.set('test','fdf45',{ expires: 1 })
  }

}
</script>

<style lang="scss">
 @import "./assets/reset.css";
 @import "./common/css/common.css";
  html,body,#app{
    width: 100%;
    height: 100%;
    background: #E6E9ED;
    overflow: hidden;
    -webkit-overflow-scrolling : touch
  }
  #app{
    display: flex;
    font-size: 0.14rem;
    flex-direction: column;
    .content{
      flex: 1;
      overflow: auto;
      background: white;
    }
    .footer{
      width: 100%;
      height: 0.72rem;
      background: white;
      display: flex;
      justify-content: space-between;
      ul{
        width: 100%;
        height: 100%;
        display: flex;
        li{
          width: auto;
          height: 100%;
          display: flex;
          flex: 1;
          flex-direction: column;
          justify-content: center;
          align-items: center;
        }
      }
    }
  }
  .danyi{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

 .kefu{
   width: 0.52rem;
   height: 0.52rem;
   background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
   border-radius:50%;
   box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
   position: fixed;
   bottom: 1.5rem;
   display: block;
   text-align: center;
   line-height: 0.52rem;
   font-size: 0.28rem;
   color: white;
   z-index: 3;
   right: 0.11rem;
 }
 .bottom_pass{
   position: absolute;
   width: 100%;
   height: 0.5rem;
   background: white;
   bottom: 0;
   display: flex;
   line-height: 0.5rem;
   font-size: 0.8rem;
   color: #4E8DF4;
   justify-content: space-between;
   span{
     width: 1.32rem;
     display: block;
     text-align: left;
     font-size: 0.3rem;
   }
   span:nth-of-type(1){
     text-align: right;
   }
 }
</style>



